import * as THREE from 'three';

import { useRef } from 'react';
import { useFrame } from '@react-three/fiber';
import { useTexture } from '@react-three/drei';

interface Props {
  position?: [number, number, number];
}

function Image3D({ position }: Props) {
  const ref = useRef<THREE.Mesh>();

  const materialImgs = useTexture([...Array(6)].map((_, i) => `textures/choon-cartoon${i + 1}.jpg`));

  useFrame(() => {
    ref.current!.rotation.x += 0.005;
    ref.current!.rotation.y += 0.005;
  });

  return (
    <mesh ref={ref} position={position} scale={1}>
      <boxGeometry args={[1.3, 1.3, 1.3]} />
      {materialImgs.map((material, index) => {
        return <meshBasicMaterial key={index} attachArray="material" map={material} />;
      })}
    </mesh>
  );
}

export default Image3D;
